<template>
  <div>
    <div class="sort-content">
      <header class="header">
        <div class="search-container">
          <el-date-picker
            type="date"
            v-model="startTime"
            placeholder="开始时间"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
          />
          <el-date-picker
            v-model="endTime"
            type="date"
            placeholder="结束时间"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
          />
          <div class="bus_name">
            <el-input
              placeholder="请输入手机号码或昵称"
              v-model="pName"
            />
          </div>
          <el-select
            style="width: 150px"
            v-model="value"
            placeholder="请选择星级节点"
            clearable
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
          <el-select
            style="width: 120px"
            v-model="selectedStatus"
            placeholder="请选择状态"
            clearable
          >
            <el-option
              v-for="item in statusList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
          <div
            class="search-btn"
            @click="serach"
          >
            确定
          </div>
        </div>
      </header>
      <el-table
        :data="userData.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column
          prop="name"
          label="会员名称"
        />
        <el-table-column label="会员头像">
          <template slot-scope="scope">
            <img
              style="width: 50px; height: 50px"
              :src="scope.row.avater"
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="mobile"
          label="手机号"
        />
        <el-table-column
          label="状态"
          prop="desStatus"
        />
        <el-table-column
          label="会员等级"
        >
          <template slot-scope="scope">
            <div>{{ scope.row.level | levelWord }}  </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="created_at"
          label="创建时间"
          :show-overflow-tooltip="true"
        />
        <el-table-column
          prop="updated_at"
          label="更新时间"
          :show-overflow-tooltip="true"
        />
        <el-table-column width="200px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template slot-scope="scope">
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="onChange(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="userData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },
            },
            // 用户或昵称
            pName:"",
            // 选中的商品状态
            options: [
                {
                    value: "选项1",
                    label: "普通",
                },
                {
                    value: "选项2",
                    label: "消费者",
                },
                {
                    value: "选项4",
                    label: "代理",
                },
                {
                    value: "选项5",
                    label: "总代理",
                },
            ],
            value: "",
            statusList: [
                {
                    label: "可用",
                    value: 1,
                },
                {
                    label: "禁用",
                    value: 2,
                },
            ],
            // 选中状态
            selectedStatus: "",
            // 开始时间
            startTime: "",
            // 结束时间
            endTime: "",
            scope: "scope",
            userData: [],
            limit: 10,
            pages: 1,
        };
    },
    created() {
        this.getUserList();
    },
    filters: {
        levelWord(value) {
            let userLevel = "";
            switch (value) {
            case 0:
                userLevel = "普通";
                break;
            case 1:
                userLevel = "消费者";
                break;
            case 2:
                userLevel = "代理";
                break;
            case 3:
                userLevel = "总代理";
                break;

            default:
                userLevel = "";
                break;
            }
            return userLevel;
        },
    },
    methods: {
    // 搜索
        serach() {
            // 初始化
            this.pages = 1;
            this.getUserList();
            
        },
        // 获取会员列表
        async getUserList() {
            try {
                let res = await this.axios.get("/admin/getUser", {
                    params: {
                        "limit": this.limit,
                        "pages": this.pages,
                        "keyword":this.pName,
                        "type":this.value,
                        "status":this. selectedStatus,
                        "time": JSON.stringify([ this.startTime,this.endTime  ])
                    },
                });
                this.userData = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                        });
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        async onChange(id){
            await this.axios.post("/admin/updateUserState",{
                "user_id":id
            });
            this.getUserList();
        },
        // 上一页
        prev() {
            this.pages--;
            this.getUserList();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getUserList();
        },
        // 下一页
        next() {
            this.pages++;
            this.getUserList();
        },
    },
    components: {},
};
</script>

<style lang="scss" scoped>
.sort-content {
  height: 800px;
  .header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    .search-container {
      display: flex;
      justify-content: space-between;
      width: 1200px;
      .search-btn {
        $height: 40px;
        width: 80px;
        height: $height;
        line-height: $height;
        text-align: center;
        color: white;
        cursor: pointer;
        background: #f7961c;
        border-radius: 6px;
      }
    }
    .through {
      $height: 35px;
      color: #f7961c;
      width: 100px;
      height: $height;
      text-align: center;
      line-height: $height;
      border: 1px solid #f7961c;
      border-radius: 6px;
      cursor: pointer;
    }
  }
  .pagination {
    margin-top: 15px;
  }
}
</style>
